// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/pagination';

// Import Swiper core and required modules
import Swiper from 'swiper';
import { Pagination, Autoplay, EffectFade, Keyboard, Navigation } from 'swiper/modules';

// Initialize Swiper when the DOM is fully loaded
document.addEventListener('DOMContentLoaded', () => {
    const swiperEl = document.querySelector('.swiper');

    if (!swiperEl) {
        console.warn('Swiper element not found');
        return;
    }

    // Collect slide meta for pagination and CTA
    const slideEls = Array.from(swiperEl.querySelectorAll('.swiper-slide'));
    const titles = slideEls.map((el, i) => el.getAttribute('data-title') || `Slide ${i + 1}`);
    const urls = slideEls.map((el) => el.getAttribute('data-url') || '#');

    // Counter elements for 01/04 display
    const counterEl = document.querySelector('.swiper-counter');
    const counterCurrentEl = document.querySelector('.swiper-counter-current');
    const counterTotalEl = document.querySelector('.swiper-counter-total');
    const pad2 = (n) => String(n).padStart(2, '0');

    // CTA button that should link to current slide
    const ctaEl = document.querySelector('.js-swiper-cta');

    new Swiper(swiperEl, {
        modules: [Pagination, Autoplay, EffectFade, Keyboard, Navigation],
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        loop: true,
        speed: 700,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        allowTouchMove: false,
        pagination: {
            el: '.swiper-pagination',
            type: 'custom',
            renderCustom: (swiper, current, total) => {
                const idx = (current - 1) % titles.length;
                const title = titles[idx] || `Slide ${current}`;
                return `<span class="swiper-pagination-title">${title}</span>`;
            }
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        on: {
            init(swiper) {
                if (counterTotalEl) counterTotalEl.textContent = pad2(titles.length);
                if (counterCurrentEl) counterCurrentEl.textContent = pad2(swiper.realIndex + 1);
                if (ctaEl) {
                    const idx = swiper.realIndex % urls.length;
                    ctaEl.setAttribute('href', urls[idx] || '#');
                }
            },
            slideChange(swiper) {
                if (counterCurrentEl) counterCurrentEl.textContent = pad2(swiper.realIndex + 1);
                if (ctaEl) {
                    const idx = swiper.realIndex % urls.length;
                    ctaEl.setAttribute('href', urls[idx] || '#');
                }
            }
        },
        keyboard: {
            enabled: true,
            onlyInViewport: false,
        },
    });
});